.navbar-brand{
    display: flex;
    justify-content: center;
    align-items: center;
}
.bar{
    display: flex;
    height:3rem;
}
.options a{
    margin-left: 2rem;
}



/* The themes */
/* THEME ONE */

.themeone .bar{
    background-color: #00abe1;
    color: #f8f8f8;
    
}

.themeone .mybutton{
    background-color: #f8f8f8;
}

/* THEME TWO */

.themetwo .bar{
    background-color: #fdd935;
    color: #000000;

}
.themetwo .box1{
    background-color: #f8f8f8;
    color:#000000;
    border: 4px solid #000000;
}
.themetwo h1{
    color: #000000;
}
.themetwo .box2{
    background-color: #f8f8f8;
    color:#000000;
    border: 4px solid #000000;

}



.themetwo button{
    background-color: #000000;
    color: #f8f8f8;

}



/* THEME THREE */

.themethree {
  color: #f8f8f8;
}
.themethree .bar{
    background-color: #ef0d50;
}
.themethree h2{
    color: #000000;
}


.themethree a{
    color: #f8f8f8;
}
