.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Blank{
  color:rebeccapurple;
}
.heading{
  background-color: #a3ffd6;
  color:#8576ff;
  display: inline;
  margin: 1rem;
  border-radius: 1rem;
  box-shadow: 2px 3px 5px #8576ff;
  padding: 1rem;

}

/* Buttons on hover the border shown */

#buttonone:hover{
  border: 3px solid #00ABE1;
  border-radius: 50%;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
